body {
  min-width: 360px;
  margin: 0;
}

::-webkit-scrollbar {
  display: none;
}

a:link, a:visited, a:active {
  color: #fff;
  text-decoration: none;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.header {
  height: 80px;
  background-color: #c51b1b;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header .logo {
  width: 4%;
}

.header .logo img {
  width: 100%;
}

.header .options {
  color: #fff;
  width: 12%;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  margin-left: 60%;
  display: flex;
}

.header .options .option {
  cursor: pointer;
  background-color: #9d1515;
  border-radius: 10px;
  padding: 3%;
  font-size: 20px;
  box-shadow: 0 0 5px 5px #00000026;
}

.header .user {
  color: #fff;
  align-self: center;
  padding-right: 1.5%;
  font-size: 40px;
}

.daily-debate {
  flex-direction: column;
  align-items: center;
  display: flex;
  position: relative;
}

.daily-debate img {
  filter: blur(2.5px);
  width: 65%;
}

.daily-debate .text {
  color: #fff;
  text-align: center;
  font-size: 70px;
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.daily-debate .commentary {
  color: #fff;
  width: 40%;
  height: 20%;
  flex-direction: row;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.daily-debate .commentary .profile {
  width: 25%;
  background-color: #263aa9;
  justify-content: center;
  align-items: center;
  font-size: 65px;
  display: flex;
}

.daily-debate .commentary .profile i {
  background-color: #1e3095;
  border-radius: 50%;
  padding: 14%;
}

.daily-debate .commentary .comment {
  width: 75%;
  background-color: #2c47e2;
  justify-content: center;
  align-items: center;
  display: flex;
}

.daily-debate .commentary .comment h1 {
  padding-left: 2%;
}

